<template>
    <div>
        {{msg}}
        <div><HelloWorld :vv="msg"/></div>
    </div>
    
</template>

<script>
import HelloWorld from '../components/guangdongshuzi.vue'
import { setInterval } from 'timers';
export default {
  name: 'hua',
  data () {
    return {
      msg: 1
    }
  },
  components: {
    HelloWorld
  },
  mounted() {
      setInterval(this.setmsg,2000)
  },
  methods:{
      setmsg(){
          this.msg+=Math.round(Math.random()*10);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped>

</style>



