<template>
        <!--  -->
        <div style="width:500px;height:100px">
            <video-player  class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions"
            ></video-player>

            <video-player  class="video-player vjs-custom-skin"
                ref="videoPlayer"
                :playsinline="true"
                :options="playerOptions2"
            ></video-player>
        </div>
</template>
<script>
import "vue-video-player/node_modules/video.js/dist/video-js.css"

require('vue-video-player/src/custom-theme.css');
//import 'videojs-flash'
import poster from "../assets/img/i-xrays.png";
import src from "../assets/music.mp4";
import src2 from "../assets/music2.mp4";
export default {
  name: 'sin',
  data () {
    return {
        playerOptions : {
            playbackRates: [0.7, 1.0, 1.5, 2.0,10], //播放速度
            autoplay: false, //如果true,浏览器准备好时开始回放。
            muted: false, // 默认情况下将会消除任何音频。
            loop: false, // 导致视频一结束就重新开始。
            preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
            language: 'zh-CN',
            aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
            fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
            sources: [{
            //type: "rtmp/mp4",
           // src: "rtmp://live.hkstv.hk.lxdns.com/live/hks" //url地址
            src:src
            }],
           // techOrder: ['flash'],
            poster: poster, //你的封面地址
             width: document.documentElement.clientWidth,
            notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
            controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //全屏按钮
            }
        },
        playerOptions2 : {
            playbackRates: [0.7, 1.0, 1.5, 2.0,10], //播放速度
            autoplay: false, //如果true,浏览器准备好时开始回放。
            muted: false, // 默认情况下将会消除任何音频。
            loop: false, // 导致视频一结束就重新开始。
            preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
            language: 'zh-CN',
            aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
            fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
            sources: [{
            //type: "rtmp/mp4",
           // src: "rtmp://live.hkstv.hk.lxdns.com/live/hks" //url地址
            src:src2
            }],
           // techOrder: ['flash'],
            poster: poster, //你的封面地址
             width: document.documentElement.clientWidth,
            notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
            controlBar: {
            timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //全屏按钮
            }
        }
    }
  },
  components: {
    
  },
  mounted() {
     
  },
  methods:{
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style> 
 
 
 
 


